export default function Icon({
  name,
  size,
  color,
  shade = 500,
  className = '',
  ...props
}) {
  if (!name) return null

  const classString = [
    'icon',
    size && `icon-size-${size}`,
    color && `text-color-${color}-${shade}`,
    className
  ]
    .filter(Boolean)
    .join(' ')

  return (
    <svg {...props} className={classString} aria-hidden="true" role="img">
      <use href={`#icon-${name}`} />
    </svg>
  )
}
